function Header() {
	let dropDownState = false;
	return {
		actions: function(){
			$('.js-header-menu').on('click', function(event){
				event.stopPropagation();
				console.log('show drop')
				$('#js-header-menu-drop').toggle()
			});
			$(window).on('click', function(){
				$('#js-header-menu-drop').hide();
			})
		},
		init: function(){
			console.log('header init')
			this.actions();
		}
	}
}
export default Header


