function Alerts(){
    const s = {
      confirmId: 'alert-confirm',
      rejectId: 'alert-reject'
    };
    // function timer(){
    //
    // }
    const defaultTime = 2000;
    return {
        hideAllAlerts: function(){
            $('.alert-wrap').fadeOut(function(){
                $('.l-page').removeClass('l-page--alert');
            });
        },
        showConfirmedAlert: function (text, time) {
             if (!time) {
                 time = defaultTime
             }
            this.hideAllAlerts();
            let template = $(`#${s.confirmId}`);
            let textTag = template.find('.alert__body span');
            let alert = template.find('.alert');
            alert.hide();
            if (!text){
                text = textTag.data('default');
            }
            textTag.html(text);
            template.css('display', 'flex');
            alert.fadeIn();
            $('.l-page').addClass('l-page--alert');
            setTimeout(this.hideAllAlerts, time)
        },
        showRejectAlert: function (text) {
            this.hideAllAlerts();
            let template = $(`#${s.rejectId}`);
            let alert = template.find('.alert');
            alert.hide();
            let textTag = template.find('.alert__body span');
            textTag.html(text);
            template.css('display', 'flex');
            console.log(alert);
            alert.fadeIn();
            $('.l-page').addClass('l-page--alert');
            setTimeout(this.hideAllAlerts, time)
        },

        actions: function(){
            $(window).on('click', () => {
                this.hideAllAlerts();
            });
        },
        
        init: function(){
            this.actions();
        }
    }
}

window.Alerts = Alerts;
export default Alerts


